var that;
export default {
	data() {
		return {
			tabbar: ["商家项目", "商家商品"],
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0 ,//tab标题的滚动条位置
			modalMPShow:false,
			tabs: ["商家项目", "商家商品"],
			active: 0,
			images: [''],
			dotStyle: false,
			seller: {},
			id:0
		}
	},
	onShow() {},
	onLoad(e) {
		that = this;
		this.id=e.id;
		this.sellerinfo();
		//  高度自适应
		uni.getSystemInfo({
			success: function(res) {
				let calc = res.windowHeight;
				that.winHeight = calc;
			}
		});
	},
	methods: {
		// 滚动切换标签样式
		switchTab: function(e) {
			let that = this;
			this.currentTab = e.detail.current;
			this.checkCor();
		},
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			if (this.currentTab > 2) {
				//这里距离按实际计算
				this.scrollLeft = 200
			} else {
				this.scrollLeft = 0
			}
		},
		callPhone(phone){
			uni.makePhoneCall({
				phoneNumber:phone
			})
		},
		ToastMsg(){
			this.modalMPShow=true;
		},
		sellerinfo() {
			this.$api.postWithData(this.api.sellerinfo, {id: this.id},
				function callbacks(res) {
					that.seller = res.code;
					// console.log(that.seller);
				})
		},
		openLocation(seller) {
			uni.openLocation({
				latitude: Number(seller.seller.latitude),
				longitude: Number(seller.seller.longitude),
				name: seller.seller.title,
				address: seller.seller.address,
			})
		},
		activeTab(res) {
			this.active = res.detail.index;
			// console.log(res.detail.index)
			// console.log(res.detail.title)
		},
	}
}
